const state = {
  user: {},
  authenticated: false,
  is_admin: false,
  login_error_message: '',
}

const getters = {
  user: state => state.user,
  authenticated: state => state.authenticated,
  login_error_message: state => state.login_error_message,
  is_admin: state => state.is_admin

}

const actions = {
  login: function ( {
    commit
  }, loginData) {
    if (loginData.username && loginData.password) {
      if (loginData.username === 'admin' && loginData.password === 'admin') {
        commit('LOGIN_AS_ADMIN', loginData)
      } else {
        commit('LOGIN', loginData)
      }
    } else {
      commit('LOGIN_ERROR')
    }
  },
  logout: function ( {
    commit
  }) {
    commit('LOGOUT')
  },
  updateUserInfo: function ( {
    commit
  }, userInfo) {
    commit('UPDATE_INFO', userInfo)
  }
}

const mutations = {
  LOGIN(state, loginData) {
    state.authenticated = true,
    state.user = loginData
  },
  LOGIN_AS_ADMIN(state, loginData) {
    state.authenticated = true,
    state.user = loginData
    state.is_admin = true
  },
  LOGIN_ERROR(state) {
    state.login_error_message = 'Enter valid credentials'
  },
  LOGOUT(state) {
    state.user = {},
    state.authenticated = false,
    state.login_error_message = ''
  },
  UPDATE_INFO(state, userInfo) {
    state.user = userInfo
    alert('Info Updated successful')
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}